<template>
  <div>
    <v-calendar
      view="weekly"
      :weekdays="[1, 2, 3, 4, 5, 6, 7]"
      :attributes="attrs"
      :min-date="startOfWeek"
      :max-date="endOfWeek"
    />
  </div>
</template>

<script>
import { startOfWeek, endOfWeek } from 'date-fns'

export default {
  data() {
    return {
      startOfWeek: startOfWeek(new Date(), { weekStartsOn: 1 }), //   ()
      endOfWeek: endOfWeek(new Date(), { weekStartsOn: 1 }), //   ()
      attrs: [] //     
    }
  }
}
</script>
