// SignupScript.js
export default {
  data() {
    return {
      email: '',
      authCode: '', //   
      password: '',
      confirmPassword: '',
      nickname: '',
      isEmailValid: true,
      isPasswordValid: true,
      emailSent: false,
      canSubmit: false //    
    }
  },
  methods: {
    validateEmail() {
      const emailRegex = /^[a-zA-Z0-9._%+-]+@dgu\.ac\.kr$/
      this.isEmailValid = emailRegex.test(this.email)
      this.updateSubmitStatus()
    },

    validatePassword() {
      const passwordRegex =
        /^(?=.*[A-Za-z])(?=.*\d|.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{8,}$/
      this.isPasswordValid = passwordRegex.test(this.password)
      this.updateSubmitStatus()
    },

    checkAuthCodeLength() {
      //    
      this.authCodeValid = this.authCode.length === 6
      this.updateSubmitStatus()
    },

    updateSubmitStatus() {
      //     canSubmit 
      this.canSubmit =
        this.email &&
        this.isEmailValid &&
        this.authCode.length === 6 &&
        this.password &&
        this.isPasswordValid &&
        this.password === this.confirmPassword &&
        this.nickname
    },

    sendVerificationEmail() {
      this.validateEmail()
      if (this.isEmailValid) {
        fetch(`${process.env.VUE_APP_BE_API_URL}/api/users/signup/send-email`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ email: this.email })
        })
          .then((response) => response.text())
          .then((data) => {
            if (data.includes('   .')) {
              this.emailSent = true
              alert(data)
            } else {
              alert('  .  .')
            }
          })
          .catch((error) => {
            console.error('Failed to send verification email:', error)
          })
      } else {
        alert(' @dgu.ac.kr  .')
      }
    },

    onSubmit() {
      this.validateEmail()
      this.validatePassword()

      if (this.isEmailValid && this.isPasswordValid && this.authCode) {
        console.log('Request Payload:', {
          email: this.email,
          authCode: this.authCode,
          password: this.password,
          nickname: this.nickname
        })

        fetch(`${process.env.VUE_APP_BE_API_URL}/api/users/signup`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            email: this.email,
            authCode: this.authCode,
            password: this.password,
            nickname: this.nickname
          })
        })
          .then((response) => {
            if (!response.ok) {
              return response.text().then((errorText) => {
                console.error('Server Error Response:', errorText)
                throw new Error(errorText || '  .')
              })
            }
            return response.text()
          })
          .then((data) => {
            if (data === ' ') {
              alert(data)
              this.$router.push('/login')
            } else {
              alert(data)
            }
          })
          .catch((error) => {
            console.error('Failed to register:', error)
            alert(error.message || '   .')
          })
      } else {
        if (!this.isEmailValid) {
          alert('   .')
        }
        if (!this.isPasswordValid) {
          alert(
            '  8, , ,   2  .'
          )
        }
        if (!this.authCode) {
          alert('  .')
        }
      }
    }
  },

  watch: {
    email: 'validateEmail',
    password: 'validatePassword',
    confirmPassword: 'updateSubmitStatus',
    authCode: 'checkAuthCodeLength',
    nickname: 'updateSubmitStatus'
  }
}
