import MainHeader from '../../../components/layout/Header.vue';
import MainFooter from '../../../components/layout/Footer.vue';
import { ref, computed, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import axios from 'axios';

export default {
  components: {
    MainHeader,
    MainFooter
  },
  setup() {
    const route = useRoute();
    const portfolioId = route.params.id;
    const activityName = ref('');
    const activityDate = ref('');
    const selectedTags = ref([]);
    const tags = ref([
      '', '', '', '', '', '', '', '',
      '', '', '', '/', '', '', '',
      '', '', '',
    ]);
    const images = ref([
      { previewUrl: "https://via.placeholder.com/150" } //  
    ]);
    const star = ref({
      situation: '',
      task: '',
      action: '',
      result: '',
    });
    const pmi = ref({
      plus: '',
      minus: '',
      interesting: '',
    });
    const isDropdownOpen = ref(false);

    //  
    const toggleDropdown = () => {
      isDropdownOpen.value = !isDropdownOpen.value;
    };

    //  /
    const toggleTag = (tag) => {
      const index = selectedTags.value.indexOf(tag);
      if (index > -1) {
        selectedTags.value.splice(index, 1);
      } else {
        selectedTags.value.push(tag);
      }
    };

    //     
    const autoResize = (event) => {
      const textarea = event.target;
      textarea.style.height = 'auto';
      textarea.style.height = `${textarea.scrollHeight}px`;
    };

    //   
    const fetchPortfolioById = async () => {
      try {
        const response = await fetch(
          `${process.env.VUE_APP_BE_API_URL}/api/portfolios/${portfolioId}`
        );

        if (!response.ok) {
          throw new Error(`Failed to fetch portfolio: ${response.statusText}`);
        }

        const data = await response.json();
        activityName.value = data.activityName || '';
        activityDate.value = data.activityDate || '';
        selectedTags.value = data.selectedTags || [];
        star.value = data.star || { situation: '', task: '', action: '', result: '' };
        pmi.value = data.pmi || { plus: '', minus: '', interesting: '' };
        images.value = data.images || [];
      } catch (error) {
        console.error('Error fetching portfolio:', error);
        alert('    .');
      }
    };

    const handleFileChange = (event) => {
      const selectedFiles = Array.from(event.target.files);
      if (images.value.length + selectedFiles.length > 5) {
        alert(' 5    .');
        return;
      }
    
      const newImagesPromises = selectedFiles.map((file) => {
        const previewUrl = URL.createObjectURL(file);
        const imageElement = new Image();
        imageElement.src = previewUrl;
    
        return new Promise((resolve) => {
          imageElement.onload = () => {
            const aspectRatio = imageElement.width / imageElement.height;
            let containerWidth, containerHeight;
    
            if (aspectRatio > 1) {
              containerWidth = '300px';
              containerHeight = `${300 / aspectRatio}px`;
            } else {
              containerWidth = `${300 * aspectRatio}px`;
              containerHeight = '300px';
            }
    
            resolve({
              file,
              name: file.name,
              size: file.size,
              previewUrl,
              containerWidth,
              containerHeight,
            });
          };
        });
      });
    
      Promise.all(newImagesPromises).then((newImages) => {
        const uniqueNewImages = newImages.filter(
          (newImage) =>
            !images.value.some(
              (existingImage) => existingImage.previewUrl === newImage.previewUrl
            )
        );
    
        if (uniqueNewImages.length === 0) {
          alert('  .');
          return;
        }
    
        images.value = [...images.value, ...uniqueNewImages];
      });
    };
    
    const removeImage = (index) => {
      URL.revokeObjectURL(images.value[index].previewUrl);
      images.value.splice(index, 1);
    };
    
    const prefix = 'images';
    const uploadImages = async () => {
      const uploadedUrls = [];
      for (const image of images.value) {
        try {
          const { data } = await axios.post(
            `${process.env.VUE_APP_BE_API_URL}/file/${prefix}/presigned-url`,
            {
              fileName: image.name,
              fileType: image.file.type,
            }
          );
    
          await axios.put(data.url, image.file, {
            headers: { 'Content-Type': image.file.type },
          });
    
          const uploadedUrl = data.url.split('?')[0];
          uploadedUrls.push(uploadedUrl);
        } catch (error) {
          console.error('Error uploading image:', error);
          alert('    .');
        }
      }
    };
    

    //      
    const saveData = async () => {
      if (!isFormComplete.value) {
        alert('  .');
        return;
      }

      try {
        //     URL  
        const response = await fetch(
          `${process.env.VUE_APP_BE_API_URL}/api/portfolios/${portfolioId}`,
          {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              activityName: activityName.value,
              activityDate: activityDate.value,
              selectedTags: selectedTags.value,
              star: star.value,
              pmi: pmi.value,
              images: images.value.map(image => image.name), //      
            }),
          }
        );

        if (!response.ok) {
          throw new Error(`Failed to save data: ${response.statusText}`);
        }

        alert('  .');
      } catch (error) {
        console.error('Error saving data:', error);
        alert('    .');
      }
    };

    const isFormComplete = computed(() => {
      return (
        activityName.value &&
        activityDate.value &&
        selectedTags.value.length > 0 &&
        Object.values(star.value).every((field) => field) &&
        Object.values(pmi.value).every((field) => field)
      );
    });

    //     
    onMounted(() => {
      fetchPortfolioById();
    });

    return {
      activityName,
      activityDate,
      tags,
      selectedTags,
      isDropdownOpen,
      star,
      pmi,
      images,
      toggleDropdown,
      toggleTag,
      autoResize,
      handleFileChange, 
      removeImage,      
      uploadImages,     
      saveData,
      isFormComplete
    };
  }
};
