import axios from 'axios';
import MainHeader from '../../../components/layout/Header.vue';
import MainFooter from '../../../components/layout/Footer.vue';

export default {
  components: {
    MainHeader,
    MainFooter
  },
  data() {
    return {
      activityName: '',
      activityDate: '',
      tags: [
        '', '', '', '', '', '', '', '',
        '', '', '', '/', '', '', '',
        '', '', ''
      ],
      selectedTags: [''],
      isDropdownOpen: false,
      star: {
        situation: '',
        task: '',
        action: '',
        result: ''
      },
      pmi: {
        plus: '',
        minus: '',
        interesting: ''
      },
      tooltipVisible: false,
      images: [], //    
      uploadedImageUrls: [] //   URL 
    };
  },
  computed: {
    isFormComplete() {
      return (
        this.activityName &&
        this.activityDate &&
        this.selectedTags.length > 0 &&
        Object.values(this.star).every(field => field) &&
        Object.values(this.pmi).every(field => field)
      );
    }
  },
  methods: {
  toggleDropdown() {
    this.isDropdownOpen = !this.isDropdownOpen;
  },
  toggleTag(tag) {
    const index = this.selectedTags.indexOf(tag);
    if (index > -1) {
      this.selectedTags.splice(index, 1);
    } else {
      this.selectedTags.push(tag);
    }
  },
  autoResize(event) {
    const textarea = event.target;
    textarea.style.height = 'auto';
    textarea.style.height = `${textarea.scrollHeight}px`;
  },
  handleFileChange(event) {
  const selectedFiles = Array.from(event.target.files);

  if (this.images.length + selectedFiles.length > 5) {
    alert(' 5    .');
    return;
  }

  const newImagesPromises = selectedFiles.map(file => {
    const previewUrl = URL.createObjectURL(file);
    const imageElement = new Image();
    imageElement.src = previewUrl;

    return new Promise(resolve => {
      imageElement.onload = () => {
        const aspectRatio = imageElement.width / imageElement.height;
        let containerWidth, containerHeight;

        if (aspectRatio > 1) {
          containerWidth = '300px';
          containerHeight = `${300 / aspectRatio}px`;
        } else {
          containerWidth = `${300 * aspectRatio}px`;
          containerHeight = '300px';
        }

        resolve({
          file,
          name: file.name,
          size: file.size,
          previewUrl,
          containerWidth,
          containerHeight
        });
      };
    });
  });

  Promise.all(newImagesPromises).then(newImages => {
    const uniqueNewImages = newImages.filter(newImage => 
      !this.images.some(existingImage => existingImage.previewUrl === newImage.previewUrl)
    );

    if (uniqueNewImages.length === 0) {
      alert('  .');
      return;
    }

    this.images = [...this.images, ...uniqueNewImages];
  });
},
      removeImage(index) {
        //     URL 
        URL.revokeObjectURL(this.images[index].previewUrl);
        this.images.splice(index, 1);
      },
    async uploadImages() {
      const prefix = 'images';
      const uploadedUrls = [];
      
      for (const image of this.images) {
        try {
          // 1.  URL 
          
          const { data } = await axios.post(
            `${process.env.VUE_APP_BE_API_URL}/file/${prefix}/presigned-url`,
            {
              fileName: image.name,
              fileType: image.type,
            }
          );          

          // 2.  URL   
          
          await axios.put(data.url, image.file, {
            headers: { 'Content-Type': image.file.type },
          });
          

          // 3.   URL  ( URL  URL )
          
          const uploadedUrl = data.url.split('?')[0];
          uploadedUrls.push(uploadedUrl);
  
        } catch (error) {
          console.error('Error uploading image:', error);
          alert('    .');
        }
      }
  
      this.uploadedImageUrls = uploadedUrls;
    },

    
    async saveData() {
      if (!this.isFormComplete) {
        alert('  .');
        return;
      }
      //     
      if (this.images.length > 0) {
        await this.uploadImages();
      }

      //     
      const newPortfolio = {
        title: this.activityName,
        createdDate: this.activityDate,
        tags: this.selectedTags,
        star: { ...this.star },
        pmi: { ...this.pmi },
        images: this.uploadedImageUrls //   URL 
      };

      try {
        await axios.post(
          `${process.env.VUE_APP_BE_API_URL}/api/portfolios`, 
          newPortfolio
        );

        alert('  !');

        //     (:  )
        this.$router.push('/akopolio/main');
      } catch (error) {
        console.error('Error saving portfolio:', error);
        alert('   .');
      }
    },
  
    resetForm() {
      this.activityName = '';
      this.activityDate = '';
      this.selectedTags = [''];
      this.star = { situation: '', task: '', action: '', result: '' };
      this.pmi = { plus: '', minus: '', interesting: '' };
      this.images = [];
      this.uploadedImageUrls = [];
    }
  }
};