import axios from 'axios'; 
import PaginationNav from '../paginationNav.vue';
import MainHeader from '../../../components/layout/Header.vue';
import MainFooter from '../../../components/layout/Footer.vue';

export default {
  components: {
    MainHeader,
    MainFooter,
    PaginationNav,
  },
  data() {
    return {
      searchQuery: '',
      startDate: '',
      endDate: '',
      currentPage: 1,
      itemsPerPage: 8,
      selectedTags: [],
      tags: [
        '', '', '', '', '', '',
        '', '', '', '', '',
        '/', '', '', '', '',
        '', ''
      ],
      isDropdownOpen: false,
      portfolioList: [], //     
      defaultPortfolioList: [
        {
          id: 1,
          title: '  1',
          createdDate: '2023-01-01',
          tags: ['', ''],
        },
        {
          id: 2,
          title: '  2',
          createdDate: '2023-02-15',
          tags: ['', ''],
        },
        //   
      ],
    };
  },
  computed: {
    filteredPortfolioList() {
      let filteredList = this.portfolioList;

      //   
      if (this.searchQuery) {
        filteredList = filteredList.filter(item =>
          item.title.includes(this.searchQuery)
        );
      }

      //   
      if (this.selectedTags.length > 0) {
        filteredList = filteredList.filter(item =>
          this.selectedTags.every(tag => item.tags.includes(tag))
        );
      }

      //   
      if (this.startDate || this.endDate) {
        filteredList = filteredList.filter(item => {
          const itemDate = new Date(item.createdDate);
          if (this.startDate && !this.endDate) {
            return itemDate >= new Date(this.startDate);
          } else if (!this.startDate && this.endDate) {
            return itemDate <= new Date(this.endDate);
          } else {
            return (
              itemDate >= new Date(this.startDate) &&
              itemDate <= new Date(this.endDate)
            );
          }
        });
      }

      //  
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return filteredList.slice(startIndex, endIndex);
    },

    totalPages() {
      return Math.ceil(this.portfolioList.length / this.itemsPerPage);
    },
  },
  methods: {
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen;
    },

    toggleTag(tag) {
      const index = this.selectedTags.indexOf(tag);
      if (index > -1) {
        this.selectedTags.splice(index, 1);
      } else {
        this.selectedTags.push(tag);
      }
      this.applyFilters();
    },

    applyFilters() {
      this.currentPage = 1;
    },

    handlePageChanged(newPage) {
      this.currentPage = newPage;
    },

    goToCreatePage() {
      this.$router.push('/akopolio/create');
    },

    resetFilters() {
      this.searchQuery = '';
      this.startDate = '';
      this.endDate = '';
      this.selectedTags = [];
      this.currentPage = 1;
    },

    goToDetailPage(id) {
      this.$router.push(`/akopolio/detail/${id}`);
    },

    async fetchPortfolios() {
      try {
        const response = await axios.get(`${process.env.VUE_APP_BE_API_URL}/api/portfolios`);
        this.portfolioList = response.data.length ? response.data : this.defaultPortfolioList; //     
      } catch (error) {
        console.error('Error fetching portfolios:', error);
        this.portfolioList = this.defaultPortfolioList; //     
      }
    },
  },

  mounted() {
    this.fetchPortfolios();
  }
};
