    </div>
  </div>
</template>

<script setup>
import MainHeader from '@/components/layout/Header.vue'
import MainFooter from '@/components/layout/Footer.vue'
import { ref, computed, onMounted } from 'vue'
import axios from 'axios'

// MyPageScript.js    
import {
  user,
  password,
  confirmPassword,
  currentPassword,
  passwordVerified,
  handleLogout,
  fetchUser,
  verifyCurrentPassword,
  updatePassword,
  updateProfile
} from './MypageScript.js'

const goals = ref([]) //  
const isExpanded = ref(false) //   
const maxVisibleGoals = 3 //    

//   
const fetchGoals = async () => {
  try {
    const response = await axios.get(
      `${process.env.VUE_APP_BE_API_URL}/api/goals`,
      { withCredentials: true }
    )
    goals.value = response.data
  } catch (error) {
    console.error('    .', error)
    //       
    goals.value = [
      { content: '  1', createdAt: '2024-10-01' },
      { content: '  2', createdAt: '2024-10-15' },
      { content: '  3', createdAt: '2024-11-05' }
    ]
  }
}

//      
onMounted(() => {
  fetchGoals()
  fetchUser() // MyPageScript.js     
})

//      
const displayedGoals = computed(() => {
  return isExpanded.value ? goals.value : goals.value.slice(0, maxVisibleGoals)
})

//    (MM.DD )
const formatDate2 = (date) => {
  const options = { month: '2-digit', day: '2-digit' }
  return new Date(date).toLocaleDateString('ko-KR', options).replace(/\//g, '.')
}

// /  
const toggleExpand = () => {
  isExpanded.value = !isExpanded.value
}

//  
const saveProfile = () => {
  const profileData = {
    name: user.name,
    email: user.email,
    nickname: user.nickname,
    studentNumber: user.studentNumber,
    major: user.major,
    introduce: user.introduce
  }
  updateProfile(profileData)
}
</script>
