/* eslint-disable */
import { createRouter, createWebHashHistory } from 'vue-router'
import AuthLayout from '@/components/layout/AuthLayout.vue' //    
import PortfolioLayout from '@/components/layout/PortfolioLayout.vue' //    
import store from '@/store'
import FeedLayout from '@/components/layout/FeedLayout.vue'
import akopolioCreate from '@/views/akopolio/create/akopolioCreate.vue'
import akopolioMain from '@/views/akopolio/main/akopolioMain.vue'


//    (src/views   .vue  )
function importAllViews() {
  const viewFiles = require.context('@/views', true, /\.vue$/)
  const views = {}

  //    
  viewFiles.keys().forEach((filePath) => {
    const viewName = filePath
      .split('/')
      .pop() //   
      .replace('.vue', '') // .vue  

    //     
    views[viewName] = viewFiles(filePath).default
  })

  return views
}

//    Vue  
const importedViews = importAllViews()

const routes = [
  {
    path: '/',
    name: 'home',
    component: importedViews['HomeView'], //   
    redirect: '/auth/login' //   /auth/login 
  },
  {
    path: '/about',
    name: 'about',
    component: importedViews['AboutView'] //   
  },
  {
    path: '/auth',
    component: AuthLayout, //  
    children: [
      {
        path: 'signup',
        name: 'SignupView',
        component: importedViews['SignupView'] //   
      },
      {
        path: 'login',
        name: 'LoginView',
        component: importedViews['LoginView'] //   
      }
    ]
  },
  {
    path: '/akopolio',
    component: PortfolioLayout, //   
    children: [
      {
        path: 'main',
        name: 'akopolioMain',
        component: importedViews['akopolioMain'] //   
      },
      {
        path: 'create',
        name: 'akopolioCreate',
        component: importedViews['akopolioCreate'] //   
      },
      {
        path: 'detail/:id',
        name: 'akopolioDetail',
        component: importedViews['akopolioDetail'] //   
      },
      {
        path: 'edit/:id',
        name: 'akopolioEdit',
        component: importedViews['akopolioEdit'] //   
      },

    ]
  },
  {
    path: '/:catchAll(.*)',
    name: 'NotFound',
    component: importedViews['NotFound'] //   
  },
  {
    path: '/mypage',
    name: 'MypageView',
    component: importedViews['MypageView'] //   
  },
  {
    path: '/main',
    name: 'MainpageView',
    component: importedViews['MainpageView'] //   
  },
  {
    path: '/calendar',
    name: 'CalendarMainView',
    component: importedViews['CalendarMainView'] //   
  },
  {
    path: '/feed',
    component: FeedLayout, //  
    children: [
      {
        path: 'ako-stamp-write',
        name: 'AkoStampWriteView',
        component: importedViews['AkoStampWriteView'] //   
      },
      {
        path: 'ako-stamp-board',
        name: 'AkoStampBoard',
        component: importedViews['AkoStampBoard'] //   
      },
      {
        path: 'ako-stamp-follow',
        name: 'AkoStampFollow',
        component: importedViews['AkoStampFollow'] //   
      },
      {
        path: 'main',
        name: 'MainFeedPage',
        component: importedViews['MainFeedPage'] //   
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory('/akoming/'), // Hash  
  routes
})

//  (  ,   )
// meta: { requiresAuth: true } //   
export default router
