<template>
  <div class="my-goal">
    <div class="goal-content">
      <span class="name">{{ nickname }}</span>
      <span class="content">{{ goalContent }}</span>
      <div class="goal-icons">
        <button @click="toggleCommentSection"><img src='../../../assets/images/comment.svg'></button>
        <div class='line'></div>
        <button @click="editGoal"><img src='../../../assets/images/write.svg'></button>
        <div class='line'></div>
        <button @click="deleteGoal"><img src='../../../assets/images/delete.svg'></button>
      </div>
    </div>

    <div v-if="showCommentSection" class="comment-section">
      <CommentInput />
      <CommentList />
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import CommentInput from './CommentInput.vue'
import CommentList from './CommentList.vue'

//    
const nickname = ref('')
const goalContent = ref('')
const showCommentSection = ref(false)
const goalId = ref('') //  ID

//   
const fetchGoalData = async () => {
  try {
    //  userId   
    const response = await fetch(`${process.env.VUE_APP_BE_API_URL}/api/goals`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'include', //   
    })

    if (response.ok) {
      const data = await response.json()
      //       
      if (data.length > 0) {
        const goal = data[0] //     (     )
        nickname.value = goal.nickname
        goalContent.value = goal.content
        goalId.value = goal.id
      }
    } else {
      alert('   .')
    }
  } catch (error) {
    console.error('  :', error)
    alert('    .')
  }
}

//   
const editGoal = async () => {
  const updatedContent = prompt(' ', goalContent.value)
  if (updatedContent && updatedContent !== goalContent.value) {
    try {
      const response = await fetch(
        `${process.env.VUE_APP_BE_API_URL}/api/goals/${goalId.value}`,
        {
          method: 'PATCH',
          headers: {
            'Content-Type': 'application/json',
          },
          credentials: 'include', //   
          body: JSON.stringify({
            content: updatedContent,
          }),
        }
      )

      if (response.ok) {
        goalContent.value = updatedContent
        alert(' .')
      } else {
        alert('    .')
      }
    } catch (error) {
      console.error('  :', error)
      alert('    .')
    }
  }
}

//   
const deleteGoal = async () => {
  const confirmDelete = confirm(' ?')
  if (confirmDelete) {
    try {
      const response = await fetch(
        `${process.env.VUE_APP_BE_API_URL}/api/goals/${goalId.value}`,
        {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
          },
          credentials: 'include', //   
        }
      )

      if (response.ok) {
        alert(' .')
        goalContent.value = '' //    
        nickname.value = ''
        goalId.value = ''
      } else {
        alert('    .')
      }
    } catch (error) {
      console.error('  :', error)
      alert('    .')
    }
  }
}

//   
onMounted(() => {
  fetchGoalData()
})

//   
const toggleCommentSection = () => {
  showCommentSection.value = !showCommentSection.value
}
</script>

<style scoped>
.my-goal {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
  border-radius: 10px;
  background: #FFF;
  height: 100%;
}

.goal-content {
  display: flex;
  width: 100%;
}

.name {
  border-radius: 10px 0px 0px 10px;
  background: #FF7F00;
  color: #FFF;
  text-align: center;
  font-family: 'NaB';
  font-size: 11px;
  width: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 0;
  word-wrap: break-word;
}

.content {
  margin-left: 10px;
  margin-right: 5px;
  padding: 5px 0px;
  color: #000;
  width: 50%;
  font-family: 'NaL';
  font-size: 15px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.goal-icons {
  display: flex;
  gap: 10px;
  align-items: center;
  background: none;
  outline: none;
}

.goal-icons button:hover {
  background: none;
}

.comment-section {
}

.line {
  width: 1px;
  height: 35px;
  background: #D9D9D9;
}
</style>
