import { reactive, ref } from 'vue'

// user  reactive 
export const user = reactive({
  email: 'example@dgu.ac.kr',
  nickname: ' ',
  studentNumber: '00000000',
  major: ' ',
  introduce: '  .'
})

//    ref 
export const password = ref('')
export const confirmPassword = ref('')
export const currentPassword = ref('')
export const passwordVerified = ref(false)

//  
export async function handleLogout() {
  try {
    //  
    const response = await fetch(
      `${process.env.VUE_APP_BE_API_URL}/api/users/logout`,
      {
        method: 'POST',
        credentials: 'include' //    
      }
    )

    if (response.ok) {
      alert('.')

      // JSESSIONID   
      document.cookie =
        'JSESSIONID=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;'

      //   
      window.location.href = '/auth/login'
    } else {
      alert(' .')
      console.error(' :', response.status, response.statusText)
    }
  } catch (error) {
    alert(' .')
    console.error(' :', error)
    this.$router.push('/auth/signup')
  }
}

//    
export async function fetchUser() {
  try {
    const response = await fetch(
      `${process.env.VUE_APP_BE_API_URL}/api/users/profile`,
      {
        method: 'GET',
        credentials: 'include' //  
      }
    )
    console.log(' :', response.data)

    if (response.ok) {
      const userData = await response.json()
      Object.assign(user, userData) // user  
    } else {
      console.error(
        '   :',
        response.status,
        response.statusText
      )
    }
  } catch (error) {
    console.error(
      '  :',
      error.response?.status,
      error.response?.data
    )
    if (error.response?.status === 401) {
      alert(' .  .')
      window.location.href = '/auth/login'
    }
  }
}

//   
export async function verifyCurrentPassword() {
  try {
    const response = await fetch(
      `${process.env.VUE_APP_BE_API_URL}/api/users/verify-password`,
      {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ password: currentPassword.value }), //  
        credentials: 'include' //  
      }
    )
    if (response.ok) {
      alert(' .')
      passwordVerified.value = true //   
    } else {
      alert('   .')
      passwordVerified.value = false //   
    }
  } catch (error) {
    console.error('  :', error)
  }
}

//  
export async function updatePassword() {
  if (!passwordVerified.value) {
    alert('   .')
    return
  }
  if (password.value !== confirmPassword.value) {
    alert('   .')
    return
  }
  try {
    const response = await fetch(
      `${process.env.VUE_APP_BE_API_URL}/api/users/password`,
      {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ password: password.value }), //  
        credentials: 'include' //  
      }
    )
    if (response.ok) {
      alert(' .')
      password.value = ''
      confirmPassword.value = ''
      currentPassword.value = ''
      passwordVerified.value = false //    
    } else {
      console.error('  :', response.status, response.statusText)
    }
  } catch (error) {
    console.error('  :', error)
  }
}

//   
export async function updateProfile(profileData) {
  const url = `${process.env.VUE_APP_BE_API_URL}/api/users/profile`
  const options = {
    method: 'PATCH',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(profileData),
    credentials: 'include' //  
  }

  try {
    const response = await fetch(url, options)
    if (!response.ok) {
      throw new Error('  ')
    }
    const data = await response.json()
    alert(' .')
    Object.assign(user, profileData)
    return data
  } catch (error) {
    console.error('  :', error)
    throw error
  }
}
