import { createStore } from 'vuex';
import auth from '../views/test/auth.js'; // auth  import
import login from './modules/login.js'; //   import
import { v4 as uuidv4 } from 'uuid'; // UUID   ID 

//    
const getStoredPortfolios = () => {
  const storedPortfolios = localStorage.getItem('portfolios');
  return storedPortfolios ? JSON.parse(storedPortfolios) : [];
};

const store = createStore({
  state: {
    portfolios: getStoredPortfolios(), //    
  },
  mutations: {
    ADD_PORTFOLIO(state, portfolio) {
      const newPortfolio = { ...portfolio, id: uuidv4() }; //  ID 
      state.portfolios.push(newPortfolio); //  
      localStorage.setItem('portfolios', JSON.stringify(state.portfolios)); //   
    },
    DELETE_PORTFOLIO(state, portfolioId) {
      state.portfolios = state.portfolios.filter(portfolio => portfolio.id !== portfolioId);
      localStorage.setItem('portfolios', JSON.stringify(state.portfolios)); //     
    },
     //    UPDATE_PORTFOLIO  
     UPDATE_PORTFOLIO(state, updatedPortfolio) {
      const index = state.portfolios.findIndex(portfolio => portfolio.id === updatedPortfolio.id);
      if (index !== -1) {
        state.portfolios.splice(index, 1, updatedPortfolio); //     
        localStorage.setItem('portfolios', JSON.stringify(state.portfolios)); //     
      }
    },
  },
  actions: {
    addPortfolio({ commit }, portfolio) {
      commit('ADD_PORTFOLIO', portfolio); // mutation 
    },
    deletePortfolio({ commit }, portfolioId) {
      commit('DELETE_PORTFOLIO', portfolioId);
    },
     //    updatePortfolio  
     updatePortfolio({ commit }, updatedPortfolio) {
      commit('UPDATE_PORTFOLIO', updatedPortfolio); // UPDATE_PORTFOLIO  
    },
  },
  getters: {
    getPortfolios(state) {
      return state.portfolios; //   
    },
  },
  modules: {
    login, //  
    auth,  // auth 
  },
});

export default store;


